import React, { Component } from 'react';

import AddCard from './../addCard/addCard';


export default class ItemsLi extends Component {

  
  render() {
    if (this.props.data) {
      return (
        <section className='display-item'>
          <div className='wrapper'>
             
                <AddCard myItems={this.props}/>
              
          </div>
        </section>
          )
    }
  }


}


